import { isUndefined, pick } from 'lodash';
import { useMemo } from 'react';

import type { WizardStep } from './WizardStep';

export function useStepperStepsFromWizardSteps(wizardSteps: WizardStep[], currentStepIdx: number) {
  return useMemo(
    () =>
      wizardSteps.map((wizardStep, stepIdx) => ({
        ...pick(wizardStep, ['title', 'description', 'status']),
        additionalVerticalContent: wizardStep.additionalHorizontalLayoutStepContent,
        clickEnabled: isUndefined(wizardStep.clickEnabled)
          ? isWizardStepEnabled(stepIdx, currentStepIdx, wizardStep.status)
          : wizardStep.clickEnabled,
      })),
    [currentStepIdx, wizardSteps],
  );
}

function isWizardStepEnabled(stepIdx: number, currentStepIdx: number, status: WizardStep['status']): boolean {
  return stepIdx < currentStepIdx || status === 'completed';
}
